
import { defineAsyncComponent, defineComponent } from 'vue';

export default defineComponent({
  components: {
    bIcon: defineAsyncComponent(() => import('@/components/BaseIcon.vue')),
    svgIcon: defineAsyncComponent(() => import('@/components/SvgIcon.vue')),
  },
  props: {
    type: {
      type: String,
      default: 'iconwechaticon19',
    },
    text: {
      type: String,
      default: 'icon文字',
    },
    color: {
      type: String,
      default: 'rgba(0,0,0,0.65)',
    },
    size: {
      type: String,
      default: '24px',
    },
    svgType: {
      type: Boolean,
      default: false,
    },
  },
  setup() {
    return {
    };
  },
});
