<div class="icon-card flex flex-columns align-items-center pointer"><template v-if="!svgType"><b-icon class="icon" :type="type" :color="color"></b-icon><div class="text">{{text}}</div><div class="icon-str">{{type}}</div></template><template v-else><svg-icon class="icon" :type="type" :color="color" :size="size"></svg-icon><div class="text">{{text}}</div><div class="icon-str">{{type}}</div></template></div>